/**
 * Registers a new block provided a unique name and an object defining its behavior.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-registration/
 */
import {registerBlockType} from '@wordpress/blocks';
import {__} from '@wordpress/i18n';
import api from '@wordpress/api';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * All files containing `style` keyword are bundled together. The code used
 * gets applied both to the front of your site and to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './style.scss';

/**
 * Internal dependencies
 */
import Edit from './edit';
import metadata from './block.json';
import Save from './save';
import {TextControl} from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';
import { useSelect } from '@wordpress/data';

import { useState, useEffect } from '@wordpress/element';


import {
  useBlockProps,
  ColorPalette,
  InspectorControls,
} from '@wordpress/block-editor';

/**
 * Every block starts by registering a new block type definition.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-registration/
 */
registerBlockType(metadata.name, {
  /**
   * @see ./edit.js
   */
  apiVersion: 3,
  title: 'Archives Calendar',
  icon: 'calendar',
  category: 'widgets',

  attributes: {
    exampleText: {
        type: 'string',
        default: '',
    },
},

edit: Edit,

save: () => {
    return null;
},

// Render the block's content on the server
// This function is called when the block is rendered on the server
// and should return the block's content as a string
// The block's attributes are passed as the second argument
// and can be used to generate the block's content
// In this example, the block's content is generated using the exampleText attribute
// but you can use any data source to generate the content
// such as a database or an API
// Note that this function is only called when the block is rendered on the server
// and not when it is rendered on the client
render: ( { attributes } ) => {
    const { exampleText } = attributes;
    return (
        `<div>${ exampleText }</div>`
    );
},
});
